<template>
  <div class="mycollect">
    <div class="mycollect__router">
      <router-link :class="{ mycollect__router__item:true, 'mycollect__router__item-active':item.name == currentName}"
      v-for="item in routers"
      :key="item.name"
      @click="currentName=item.name"
      :to="`/MyCollect/${item.path}`">
        {{ item.name }}
      </router-link>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'MyCollect',
  data () {
    return {
      currentName: '专辑',
      routers: [
        {
          name: '专辑',
          path: ''
        },
        {
          name: '歌手',
          path: 'ArtistList'
        },
        {
          name: '视频',
          path: 'VideoList'
        }
      ]
    }
  }
}
</script>

<style lang="scss" scoped>
.mycollect{
  padding: 20px;
  &__router{
    display: flex;
    align-items: center;
    margin-bottom: 40px;
    &__item{
      margin-right: 20px;
      font-size: 16px;
      font-weight: 300;
      color: #111111;
      &-active{
        font-weight: 600;
        font-size: 18px;
        &::after{
          display: block;
          content: '';
          margin: 0 auto;
          width: 90%;
          height: 4px;
          border-radius: 2px;
          background-color: red;
        }
      }
    }
  }
}
</style>
